<template>
  <div class="personal-content-body">
    <div class="content investPersonaInfoPublish scroll-style">
      <div class="content-title clear">
        <div class="left">信息发布</div>
      </div>
      <div class="content-body clear">
        <div class="content-body-left-box left">
          <!-- 导航栏 -->
          <div class="content-body-left-box-title">
            <!-- 导航栏 -->
            <div class="clear">
              <div class="text-nav breadcrumb left">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item :to="{ name: 'infoPublishIndex' }">信息发布</el-breadcrumb-item>
                  <el-breadcrumb-item :to="{ name: 'gpNoticePublishList' }">机构公告发布历史</el-breadcrumb-item>
                  <el-breadcrumb-item>机构公告发布</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
            </div>
            <div class="line"></div>
          </div>
          <div class="content-body-left-box-content">
            <div class="content-part-box">
              <!-- <div class="content-part-title">Part 1：自定义发布：输入文章标题，标签，文章正文完成发布</div> -->
              <div class="content-part-body">
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em class="red">*</em>
                    <em>文</em>
                    <em>章</em>
                    <em>标</em>
                    <em>题</em>
                  </div>
                  <div class="form-input left">
                    <el-input placeholder="请输入文章标题" class="header-right-time input-position" :clearable='true' v-model="commitData.title"></el-input>
                  </div>
                </div>
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em>标</em>
                    <em>签</em>
                  </div>
                  <div class="form-input left">
                    <div class="form-input-span clear">
                      <el-input placeholder="输入标签，按确定或者回车出现在下方" @change="addSpan" class="header-right-time left" :clearable='true' v-model="currentspan"></el-input>
                      <div class="form-input-span-btn btn-blue right" @click="addSpan">确定</div>
                    </div>
                    <div class="form-span-box clear">
                      <div class="form-span-item" v-for="(item,index) in commitData.spanlist">{{item}}<i class="el-icon-close" @click="deleteSpan(index)"></i></div>
                    </div>
                  </div>
                </div>
                <div class="form-group from-inputbox clear">
                  <div class="form-title left">
                    <em>附</em>
                    <em>件</em>
                  </div>
                  <div class="form-input form-filebox left">
                    <div class="file-list">
                      <div class="file-item" v-for="(item,index) in oldFile">
                        <div class="file-name">
                          <div>{{item.file_name}}</div>
                        </div>
                        <div class="file-click">
                          <input type="file" @change="delOldFile(item.file_id,index);addFile($event,-1)">替换
                        </div>
                        <div class="file-click file-click-del" @click="delOldFile(item.file_id,index)">删除</div>
                      </div>
                      <div class="file-item" v-for="(item,index) in newFile">
                        <div class="file-name">
                          <div>{{item.name}}</div>
                        </div>
                        <div class="file-click">
                          <input type="file" @change="addFile($event,index)">替换
                        </div>
                        <div class="file-click file-click-del" @click="delFile(index)">删除</div>
                      </div>
                    </div>
                    <div class="file-add">
                      <input type="file" class="file-add-input" @change="addFile($event,-1)" multiple="multiple">
                      <i class="el-icon-plus"></i>添加文件
                    </div>
                  </div>
                </div>
                <div class="form-group from-inputbox clear form-editorbox">
                  <div class="form-title left">
                    <em class="red">*</em>
                    <em>正</em>
                    <em>文</em>
                  </div>
                  <div class="form-input left">
                    <!-- <quill-editor class="form-editor" ref="myQuillEditor" v-model="commitData.content" :options="editorOption">
                    </quill-editor> -->
                    <Vueditor class="form-editor" ref="ueditor"></Vueditor>
                  </div>
                </div>
              </div>
            </div>
            <div class="content-confirm clear">
              <div class="content-confirm-btn btn-blue" :class="{'btn-blue-disabled':!submitOk}" @click="commitClick">提交</div>
              <!-- <router-link :to="{name:'gpNoticePublishList'}">
            <div class="content-confirm-btn btn-blue-border right">取消</div>
          </router-link> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { bigeventCompanyList, pubBulletin, bulletinInfo, lpNewsInfo, delNewsFile } from '../../config/api'
import progressBar from '../../components/progressbar.vue'
//import { quillEditor } from 'vue-quill-editor'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      //新闻iD
      newsId: '',
      //搜索数据
      commitData: {
        title: '',
        spanlist: [],
        content: ''
      },
      newFile: [],
      oldFile: [],
      delOldFilelist: [],
      currentspan: '', //标签输入框内容
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic'],
            //['blockquote', 'code-block'],
            [{ 'header': 1 }],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            /*[{ 'script': 'sub' }, { 'script': 'super' }],*/
            //[{ 'indent': '-1' }, { 'indent': '+1' }],
            //[{ 'direction': 'rtl' }],
            [{ 'size': ['small', false, 'large', 'huge'] }],
            //[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
            /*[{ 'font': [] }],*/
            [{ 'color': [] }],
            [{ 'align': [] }],
            ['clean'],
            ['link', 'image']
          ],
          syntax: {
            highlight: text => hljs.highlightAuto(text).value
          }
        },
        placeholder: ''
      }
    }
  },
  components: {
    progressBar
    //quillEditor
  },
  computed: {
    //编辑器修改需修改
    submitOk: function() {
      if (this.commitData.title != '') {
        let editor = this.$refs.ueditor;
        let content = '';
        content = editor.getContent();
        if(content!=''){
          return true
        }else{
          return false
        }
      } else {
        return false
      }
    }
  },
  mounted() {
    if (this.$route.query.id != undefined) {
      this.newsId = this.$route.query.id;
      if (this.newsId != -1) {
        this.getDetail();
      }
    } else {
      this.newsId = -1;
    }
    //编辑器修改需修改
    let editor = this.$refs.ueditor;
    editor.setContent(this.commitData.content);
    editor.upload = function(obj, callback) {
      var file = obj.files[0]
      var img = new Image,
        width = 1024, //image resize   压缩后的宽
        quality = 0.8, //image quality  压缩质量
        canvas = document.createElement("canvas"),
        drawer = canvas.getContext("2d");

      var reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = function() {
        img.src = this.result
        var imghtml = "<img src='" + this.result + "'>"
        imghtml = editor.getContent() + imghtml;
        editor.setContent(imghtml);
        callback('');
      }
    }
  },
  created() {

  },
  methods: {
    //获取pe详情
    getDetail() {
      bulletinInfo({
        id: this.newsId
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.commitData = {
            title: res.data.payload.data.title,
            spanlist: res.data.payload.data.tag,
            content: res.data.payload.data.content,
          };
          //编辑器修改需修改
          let editor = this.$refs.ueditor;
          editor.setContent(this.commitData.content);
          this.oldFile = res.data.payload.file;
        }
      })
    },
    //添加标签
    addSpan() {
      if (this.currentspan != '') {
        if (this.commitData.spanlist.indexOf(this.currentspan) == -1) {
          this.commitData.spanlist.push(this.currentspan)
        }
        this.currentspan = '';
      }
    },
    //删除标签
    deleteSpan(index) {
      this.commitData.spanlist.splice(index, 1)
    },
    addFile(e, i) {
      for (let val of e.target.files) {
        this.newFile.push(val);
      }
      if (i != -1) {
        this.delFile(i);
      }
      e.target.value = '';
    },
    delFile(i) {
      this.newFile.splice(i, 1);
    },
    //删除原有文件
    delOldFile(id, i) {
      this.oldFile.splice(i, 1);
      this.delOldFilelist.push(id);
    },
    //删除服务器原有文件
    delOldFileCommit() {
      if (this.delOldFilelist.length > 0) {
        this.delOldFilelist.forEach(item => {
          delNewsFile({
            file_id: item
          }).then(res => {})
        })
      }
    },
    //编辑器修改需修改
    commitClick() {
      let editor = this.$refs.ueditor;
      this.commitData.content = editor.getContent();
      if (this.commitData.content == '') {
        this.submitOk = false;
      }
      if (this.submitOk) {
        let param = {
          tag: this.commitData.spanlist.toString(),
          title: this.commitData.title,
          content: this.commitData.content
        }
        if (this.newsId != -1) {
          param = {
            id: this.newsId,
            tag: this.commitData.spanlist.toString(),
            title: this.commitData.title,
            content: this.commitData.content,
          }
        }
        this.delOldFileCommit();
        pubBulletin({ data: JSON.stringify(param) }, this.newFile).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '发布成功');
            this.$router.push({
              name: 'gpNoticePublishList'
            })
          }else{
            dialogConfirm('info',res.data.message);
          }
        })
      } else {
        dialogConfirm('info', '您还有必填项没有填写！')
      }
    }
  }
};

</script>
<style lang="scss" scoped>
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 40px 40px;
}

.content-body {
  position: absolute;
  top: 88px;
  bottom: 20px;
  left: 40px;
  right: 40px; //min-width: 1065px;
  border: 1px solid #E4E4E4;
  padding: 30px 0px 10px 16px;
  overflow: hidden;
  color: #35383A;
} //左边容器
.content-body-left-box {
  position: absolute;
  left: 16px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  overflow: hidden;
  display: inline-block;
} //内容导航栏
.text-nav {
  text-align: left;
}

.text-nav-name-lastPage {
  font-size: 14px;
  color: #3C98E8;
  cursor: pointer;
}

.text-nav-arrow {
  color: #3C98E8;
  font-size: 12px;
  position: relative;
  top: -1px;
}

.content-body-left-box-title {
  padding-top: 25px;
}

.content-body-left-box-content {
  position: absolute;
  top: 70px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: auto;
  padding-bottom: 40px;
}

.text-nav-name-nowPage {
  font-size: 14px;
} //内容部分
.content-part-box {
  display: inline-block;
  margin-top: 30px;
  margin-left: 60px;
} //头部
.content-part-title {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  margin-bottom: 13px;
}

.line {
  width: 100%;
  border-top: 1px solid #F4F4F4;
  margin-top: 25px;
}

.form-group {
  margin-bottom: 30px;
}

.form-title {
  font-size: 18px;
  color: #35383A;
  line-height: 40px;
  text-align: right;
  /* display: flex;
  justify-content: space-between;
  width: 100px; */
  width: 120px;
  margin-right: 18px;
  font-weight: bold;
  .red {
    color: #F01400;
  }
  &>em {
    font-weight: bold;
  }
}

.form-input {
  width: 490px;
}

.from-inputbox .header-right-time {
  width: 100%;
}

.form-input-span .header-right-time {
  width: 360px;
}

.form-input-span-btn {
  width: 114px;
  line-height: 40px;
  height: 40px;
  text-align: center;
}

.form-span-box {}

.form-span-item {
  margin-top: 12px;
  line-height: 24px;
  position: relative;
  background: #E7F4FF;
  color: #3C98E8;
  margin-right: 12px;
  padding: 0px 28px 0px 16px;
  font-size: 14px;
  border-radius: 30px;
  float: left;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  &>i {
    color: #3C98E8;
    position: absolute;
    line-height: 24px;
    right: 12px;
    cursor: pointer;
  }
}

.file-add {
  font-size: 14px;
  line-height: 36px;
  background: #F4F4F4;
  color: #B0B0B0;
  border: 1px solid #EDEEF3;
  width: 100%;
  display: block;
  text-align: center;
  cursor: pointer;
  position: relative;
  &>i {
    line-height: 36px;
    margin-right: 10px;
    color: #B0B0B0;
  }
  .file-add-input {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
  }
  &:hover {
    color: #fff;
    background-color: #3C98E8;
    i {
      color: #fff;
    }
  }
}

.file-list {
  .file-item:first-child {
    margin-top: 12px;
  }
}

.file-item {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 14px;
  .file-name {
    font-size: 14px;
    color: #35383A;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    overflow: hidden;
    &>div {
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-right: 20px;
      overflow: hidden;
    }
  }
  .file-click {
    display: flex;
    flex-shrink: 0;
    font-size: 14px;
    cursor: pointer;
    margin: 0px 10px;
    position: relative;
    overflow: hidden;
    &>input {
      position: absolute;
      left: 0px;
      right: 0px;
      top: 0px;
      bottom: 0px;
      opacity: 0;
      cursor: pointer;
    }
    &:hover {
      color: #3C98E8;
    }
    &.file-click-del:hover {
      color: #F01400;
    }
  }
}

.content-confirm-btn {
  line-height: 40px;
  width: 140px;
  text-align: center;
  &.btn-blue-border {
    line-height: 38px;
    margin-right: 20px;
  }
}

.form-editorbox {
  .form-input {
    width: 750px;
    height: 300px;
  }
}

.content-confirm {
  margin-left: 198px;
}

@media (max-width: 1350px) {
  .form-editorbox .form-input {
    width: 490px;
  }
}

</style>



